.p-pg {
  &-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
  &-row {
    width: calc(calc(100% / 5) - 20px);
    height: 40vh;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    margin: 0 0 20px 20px;
    background-color: $white;
    border-radius: 5px;
    transition: ease .3s all;
    @include mq(desk-lg) {
      width: calc(calc(100% / 4) - 20px);
      height: 30vh;
      min-height: 260px;
      margin: 0 0 20px 20px !important;
    }
    @include mq(desk-sm) {
      width: calc(calc(100% / 3) - 20px);
      height: 30vh;
      min-height: 260px;
      margin: 0 0 20px 20px !important;
    }
    @include mq(phone-xl) {
      width: calc(calc(100% / 2) - 40px);
      height: 30vh;
      min-height: 260px;
      margin: 0 20px 20px 20px !important;
    }
    @include mq(phone-md) {
      width: calc(calc(100%) - 40px);
      height: 30vh;
      min-height: 300px;
      margin: 0 20px 20px 20px !important;
    }
    &:hover {
      cursor: pointer;
      transform: scale(1.02);
    }
    &:first-child { 
      display: none !important; 
    }
    &:nth-child(2) {
      margin-right: 0;
    }
  }
  &-item {
    padding: 2rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    &_name {
      font-size: 1.5rem;
      font-weight: bold;
      margin-bottom: 0.5rem;
      text-transform: capitalize;
    }
    &_sprite {
      width: 50%;
      margin: auto;
      img {
        width: auto;
        max-width: 100%;
        height: auto;
        object-fit: cover;
      }
    }
  }
}